import './App.css';
import { BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Header from './Header';
import Home from './Home'
import Menu from './Menu';
import Breakfast from './Breakfast';
import Lunch from './Lunch';
import Starter from './Starter'
import Beverage from './Beverage'
import Contact from './Contact';
import Rooms from './Rooms'
import Hotel from './Hotel';
import Lawn from './Lawn';
import Login from './Login'
function App() {
  return (
    <Router >
      <div className="App">
        <Switch>
          <Route path="/Login">
            <Header />
            <Login />
         </Route> 
          <Route path="/Lawns">
            <Header />
            <Lawn />
         </Route> 
          <Route path="/Hall">
            <Header />
            <Hotel />
         </Route> 
        <Route path='/Rooms' >
              <Header />
              <Rooms />
          </Route>
        
        <Route path='/Restaurant/Starter' >
              <Header />
              <Starter />
            </Route>

            <Route path='/Restaurant/Beverages' >
              <Header />
              <Beverage />
            </Route>
        <Route path='/Restaurant/Lunch' >
              <Header />
              <Lunch />
            </Route>
        <Route path='/Restaurant/Breakfast' >
              <Header />
              <Breakfast />
            </Route>
        <Route path="/Restaurant" >
            <Header />
            <Menu />
            </Route>
            <Route path='/Contact' >
              <Header />
              <Contact />
            </Route>
          <Route path="/" >
              <Header />
              <Home /> 
          </Route>
                                     

          
          
        </Switch>
      </div>
   </Router>
  );
}

export default App;
